<template>
  <div class="pay-assistance full-screen">
    <div class="page-panel">
      <pem-scroll-view 
        class="scroll-view"
        ref="scroll">
        <div class="wrap">
           <div class="title bold first-title">如何绑定支付宝，微信或银行卡充值？</div>
           <div class="title">1、进入App Store，点击“App”滑动到底部“快速链接”</div>
           <img class="step1" src="./step1@2x.png" alt="">
           <div class="title">2、选择“绑定支付宝、微信或银行卡”</div>
           <img class="step2" src="./step2@2x.png" alt="">
           <div class="title">3、填写Apple ID密码，点击继续</div>
           <img class="step3" src="./step3@2x.png" alt="">
           <div class="title">4、选择要绑定的支付方式按要求填写后点击“完成”</div>
           <img class="step4" src="./step4@2x.png" alt="">
          <div class="title">5、验证完成后继续回到项目工场APP,继续支付，输入密码购买即可</div>
          <img class="step5" src="./step5@2x.png" alt="">
          <div class="title bold last-title">充值注意事项</div>
          <div class="title">
            充值前请确保你的手机满足如下：<br>
            1、App内购买项目访问限制为开启状态「设置-通用-访问限制」<br>
            2、您的苹果手机未越狱 <br>
            3、当前苹果账号与下载项目工场时账号一致
          </div>
          
          <pem-button text="去App Store设置" @submit="goAppStore"></pem-button>
        </div>
    </pem-scroll-view>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemButton from 'components/pem-button/pem-button'
import { setTitle } from 'common/js/util'

export default {
  data () {
    return {
    }
  },
  components: {
    PemScrollView,
    PemButton
  },
  created () {
    this._setTitle('支付帮助')
    setTimeout(() => {
      this.resizeWrapper()
    }, 50)
  },
  methods: {
    _setTitle (title) {
      setTitle(title)
    },
    created () {
      window.location.href = window.location.origin + '/static/app/pay-assistance.html'
    },
    goAppStore () {
      window.location.href = 'https://itunes.apple.com/cn/app/id1057083949?mt=8'
    },
    resizeWrapper () {
      this.$refs.scroll.refresh()
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.page-panel
  top: 0
.wrap
  padding: 0.34rem 0.3rem 1rem 0.3rem
  text-align: center
  font-size: 0
  .title
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #41403E;
    line-height: 22px
    text-align: left
    &.bold
       font-family: PingFangSC-Medium;
       font-weight: bold
    &.first-title
      margin-bottom: 25px
    &.last-title
      margin-bottom: 12px
  .step1
    width: 131px
    height: 46px
    margin: 15px 0 25px
  .step2
    width: 212px
    height: 285px
    margin: 4px 0 10px
  .step3
    width: 188px
    height: 297px
    margin: 3px 0 1px
  .step4
    width: 351px
    height: 285px
    margin: 5px 0 9px
  .step5
    width: 188px
    height: 297px
    margin: 3px 0 0
  .pem-submit
    width: 255px
    margin-top: 17px 
    border-radius: 4px
</style>



